import { FORM_DATA } from '../actions/types';

const initialState = {
    item: ''
}

export default function (state = initialState, action) {
    switch (action.type) {
        case FORM_DATA:
            return {
                ...state,
                item: action.data
            }
        default:
            return state;
    }
}


