import { FETCH_RECIPES } from './types';

const createRecipeArray = ((recipeItems) => {
    recipeItems.matches.map((recipeItem) => {
        return recipeItem.id
    })
})

export const fetchRecipes = (appId, appKey, dish) => dispatch => {
    console.log('fetching');
    console.log(appId);
    fetch('http://api.yummly.com/v1/api/recipes?_app_id=' + appId + '&_app_key=' + appKey + '&q=' + dish + '&maxResult=30&start=10')
        .then(res => res.json())
        .then(data => dispatch({
            type: FETCH_RECIPES,
            payload: data
        })).catch((err) => {
            console.log(err);
        });
    //data => console.log(data.matches))
}


