import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchRecipes } from '../actions/recipeActions';
import './css/components.css';

const appId = '85b20d33';
const appKey = '02b11329b369711fd20104a381b380e1';

class RecipeList extends Component {

    componentWillMount() {
        this.props.fetchRecipes(appId, appKey, this.props.dish);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.dish !== undefined) {
            console.log('nextprops dish:', nextProps.dish);
            this.props.fetchRecipes(appId, appKey, nextProps.dish);
        }
    }

    openRecipeLocation(recipeItem) {
        const url = fetch('https://api.yummly.com/v1/api/recipe/' + recipeItem + '?_app_id=' + appId + '&_app_key=' + appKey)
            .then(res => res.json())
            .then(data => data.attribution.url);
        console.log(url);

    }

    render() {
        return (
            <div>
                {
                    //console.log(this.props.recipes)
                    (this.props.recipes !== undefined && this.props.recipes.length !== 0) ? (
                        this.props.recipes.matches.map(recipe => (
                            <div key={recipe.id}>
                                <div className="col s12 m7">
                                    <div className="card horizontal">
                                        <div className="card-image">
                                            <img className="recipe-card-image" src={recipe.smallImageUrls[0]} />
                                        </div>
                                        <div className="card-stacked">
                                            <div className="card-content">
                                                <h4>{recipe.sourceDisplayName}</h4>
                                            </div>
                                            <div className="card-action">
                                                <a href={"http://www.yummly.co/recipe/" + recipe.id} target="_blank">{recipe.recipeName}</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))

                    )
                        :
                        <div></div>

                }
            </div>
        )
    }
}

RecipeList.propTypes = {
    recipes: PropTypes.array.isRequired,
    dish: PropTypes.string.isRequired
}

const mapStateToProps = state => ({
    recipes: state.recipes.items,
    dish: state.dish.item
})

export default connect(mapStateToProps, { fetchRecipes })(RecipeList);


