import React, { Component } from 'react';
import { connect } from 'react-redux';
import { formData } from '../actions/formDataAction';

let inputFormData = '';



class RecipeForm extends Component {

    onInputChange = (e) => {
        console.log(e.target.value);
        inputFormData = e.target.value;
    }

    onBtnClick = () => {
        if (inputFormData.length > 0) {
            this.props.formData(inputFormData);
        }
    }

    render() {
        return (
            <div>
                <input type="text" onChange={this.onInputChange}></input>
                <button className="waves-effect waves-light btn" type="btn" onClick={this.onBtnClick}>Find</button>
            </div>
        )
    }
}

export default connect(null, { formData })(RecipeForm);


